import React from "react"
import "./register.css"
import FloatingLabelInput from 'react-floating-label-input';

class Register2 extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			allergies:[],
      on_color:"rgb(255, 127, 129)",
      off_color:"rgb(243,245,248)",
      other: "",
      error: "",
      others: [],
		}
	}

  onClick = (e) => {
    let arr = this.state.allergies
    if (arr.includes(e.target.id)) {
      arr.splice(arr.indexOf(e.target.id), 1)
    } else {
      arr.push(e.target.id)
    }
    this.setState({allergies: arr})
    console.log(this.state.allergies)
  }

  onOtherClick = (e) => {
    let arr = this.state.others
    if (arr.includes(e.target.id)) {
      arr.splice(arr.indexOf(e.target.id), 1)
    } 
    this.setState({others: arr})
    console.log(this.state.others)
  }

  onChange = (e) => {
    this.setState({other: e.target.value})
  }

  onEnter = (e) => {
    let x = this.state.other;
    x = x.toUpperCase();
    if (e.key === 'Enter' && this.state.other !== "" && this.state.others.indexOf((this.state.other).toLowerCase()) === -1) {
      let arr = this.state.others;
      let x = this.state.other;
      x = x.toUpperCase();
      arr.push(x);
      this.setState({others: arr})
      this.setState({other: ""})
      this.setState({error: ""})
    } else if (this.state.others.indexOf(x) !== -1) {
      this.setState({error: "Please enter unique value"})
      this.setState({other: ""})
      console.log(this.state.others)
    }
  }

	render() {
    let {onAllergiesSubmit, onRouteChange} = this.props
    let arr = this.state.others
    let output = arr.map(x => <p onClick={this.onOtherClick} id={x} className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.others.indexOf(x) !== -1 ? this.state.on_color : this.state.off_color, color: this.state.others.includes(x) ? "white" : "gray"}}>{x}</p>)
		return(
			<div className="shadow-3 tl b--light-gray ph5 pb4 bg-white ba Avenir" style={{margin:"auto", "font-family":"Avenir", width: "max(40%, 700px)"}}>
				<p className="f1 ml3 mt4 gray mb3">ALLERGIES</p>
        <p className="f5 ml3 mt2 gray w-50 mb3">You would be surprised how much this information helps</p>
        <div className="grid-box pa0 w-100 ma0">
          <p onClick={this.onClick} id="option1" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.indexOf("option1") !== -1 ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option1") ? "white" : "gray"}}>Option 1</p>
          <p onClick={this.onClick} id="option2" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.includes("option2") ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option2") ? "white" : "gray"}}>Option 2</p>
          <p onClick={this.onClick} id="option3" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.indexOf("option3") !== -1 ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option3") ? "white" : "gray"}}>Option 3</p>
          <p onClick={this.onClick} id="option4" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.includes("option4") ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option4") ? "white" : "gray"}}>Option 4</p>
          <p onClick={this.onClick} id="option5" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.indexOf("option5") !== -1 ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option5") ? "white" : "gray"}}>Option 5</p>
          <p onClick={this.onClick} id="option6" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.includes("option6") ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option6") ? "white" : "gray"}}>Option 6</p>
          <p onClick={this.onClick} id="option7" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.indexOf("option7") !== -1 ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option7") ? "white" : "gray"}}>Option 7</p>
          <p onClick={this.onClick} id="option8" className="f4 pointer ml2 br2 ph4 pv3 mb0 mt0 dib" style={{background: this.state.allergies.includes("option8") ? this.state.on_color : this.state.off_color, color: this.state.allergies.includes("option8") ? "white" : "gray"}}>Option 8</p>
          {output}
        </div>
        <p className="mt4 ml2 mb1 gray gender">Add Additional Allergens</p>
        <div className="tl" style={{ fontSize: 20, background:"rgb(243,245,248)", padding:"20px 20px", "border-radius":"15px", width:"95%", "margin-top":"20px"}}>
                <FloatingLabelInput
                  id="other_text"
                  label="Other Allergens"
                  type = "text"
                  value = {this.state.other}
                  onChange = {this.onChange}
                  onKeyDown={this.onEnter}
                  placeholder="List them here and click enter - they should appear above"
                  style={{background:"rgb(243,245,248)"}}
                />
        </div>
        <p className="f4 mt4 red tc">{this.state.error}</p>
        <div className="grid-box">
          <p onClick = {()=>onRouteChange("account")} className="br-100 purple pv3 shadow-2 pointer w-30 mt2" style={{"margin":"auto", "margin-top":"20px", "padding-left":"1.5em", "padding-right":"2em"}}><span class="arrow arrow-left"></span></p>
          <p onClick = {()=>onAllergiesSubmit(this.state.allergies, this.state.others)} className="br-100  purple pv3 pl3 pr3 shadow-2 pointer w-30 mt2" style={{"margin":"auto", "margin-top":"20px", "padding-left":"1.25em", "padding-right":"2em"}}><span class="arrow arrow-right"></span></p>
			  </div>
      </div>
		)
	}
}

export default Register2;