import React from "react"
import { TextField } from '@material-ui/core'
import FloatingLabelInput from 'react-floating-label-input';
import "./register.css"
import Tabs from 'react-bootstrap/Tabs'
import Amplify, { Auth } from 'aws-amplify';
import awsconfig from '../aws-exports';
Amplify.configure(awsconfig);
var validator = require("email-validator");
var passwordValidator = require('password-validator');
var schema = new passwordValidator();

schema
.is().min(8)                                    // Minimum length 8
.is().max(100)                                  // Maximum length 100
.has().uppercase()                              // Must have uppercase letters
.has().lowercase()                              // Must have lowercase letters
.has().digits()
.has().symbols()                                
.has().not().spaces()                           // Should not have spaces

class Register1 extends React.Component {
	constructor(props) {
		super(props)
		this.state = {
			first_name:"",
			last_name:"",
			email:"",
			pass: "",
			age: "",
			zip: "",
			gender: "",
			error: "",
			on_color: "rgb(127, 90, 179)",
			off_color: "rgb(243,245,248)",
			success:"",
		}
	}

	onTypeEnter = (e) => {
		this.setState({[e.target.id]: e.target.value})
		console.log(e.target.value)
	}

	
	onOptionClick = (e) => {
		this.setState({gender: e.target.id})
		console.log(e.target.id)
	}

	render() {
		let {onAccountSubmit} = this.props

		const signUp = () => {
			Auth.signUp({username: this.state.email, password: this.state.pass})
				.then(()=>this.setState({success: "Account created. Please verify your account using link sent to your email."}))
				.catch((err) => {
					this.setState({error: err.message})
				})
		}
		const onSubmit = () => {
			if (this.state.age==="" || this.state.zip==="" || this.state.email==="" || this.state.pass==="") {
				this.setState({error: "Please enter all the required details."})
			} else if (validator.validate(this.state.email) !== true) {
				this.setState({error: "Email format incorrect"})
			} else if (schema.validate(this.state.pass) !== true) {
				this.setState({error: "Password not strong enough. Password must be atleast 8 characters long and include uppercase, lowercase, numeric and special characters."})
			} else {
				this.setState({error: ""})
				signUp()
			}
		}

		if (this.state.success === "Account created. Please verify your account using link sent to your email.") {
			onAccountSubmit(this.state)
		}
		return(
			<div className="shadow-3 tl b--light-gray ph5 pb4 bg-white ba Avenir" style={{margin:"auto", "font-family":"Avenir", width: "max(40%, 700px)"}}>
				<p className="f1 ml3 mt4 gray mb3">ACCOUNT</p>
				<p className="f5 ml3 mt2 gray w-80 mb3">Please enter your details. Make sure your password is atleast 8 characters long and contains uppercase, lowercase, numeric and special characters.</p>

				<div style={{"padding-top": '1.5em'}}>
		          <div className="tl" style={{ fontSize: 20, background:"rgb(243,245,248)", padding:"20px 20px", "border-radius":"15px", width:"85%"}}>
		            <FloatingLabelInput
		              id="first_name"
		              label="First Name"
		              placeholder={this.state.first_name}		         
		              onChange={this.onTypeEnter}
		              style={{background:"rgb(243,245,248)"}}
		            />
		          </div>
		          <div className="tl" style={{ fontSize: 20, background:"rgb(243,245,248)", padding:"20px 20px", "border-radius":"15px", width:"85%", "margin-top":"20px"}}>
		            <FloatingLabelInput
		              id="last_name"
		              label="Last Name"
		              placeholder={this.state.last_name}
		              onChange={this.onTypeEnter}
		              style={{background:"rgb(243,245,248)"}}
		            />
		          </div>
		          <div className="tl" style={{ fontSize: 20, background:"rgb(243,245,248)", padding:"20px 20px", "border-radius":"15px", width:"85%", "margin-top":"20px"}}>
		            <FloatingLabelInput
		              id="email"
		              label="Email Address *"
		              type = "email"
		              placeholder={this.state.email}
		              onChange={this.onTypeEnter}
		              style={{background:"rgb(243,245,248)"}}
		            />
		          </div>
		          <div className="tl" style={{ fontSize: 20, background:"rgb(243,245,248)", padding:"20px 20px", "border-radius":"15px", width:"85%", "margin-top":"20px"}}>
		            <FloatingLabelInput
		              id="pass"
		              label="Password *"
		              type = "password"
		              placeholder="Password length atleast 8 chars"
		              onChange={this.onTypeEnter}
		              style={{background:"rgb(243,245,248)"}}
		            />
		          </div>
		          <div className="tl dib" style={{ fontSize: 20, background:"rgb(243,245,248)", padding:"20px 20px", "border-radius":"15px", width:"25%", "margin-top":"20px"}}>
		            <FloatingLabelInput
		              id="age"
		              label="Age *"
		              type = "number"
		              min = "0"
		              placeholder={this.state.age}
		              onChange={this.onTypeEnter}
		              style={{background:"rgb(243,245,248)"}}
		            />
		          </div>
		          <div className="tl dib mb2" style={{ fontSize: 20, background:"rgb(243,245,248)", padding:"20px 20px", "border-radius":"15px", width:"35%", "margin-top":"20px", "margin-left":"20px"}}>
		            <FloatingLabelInput
		              id="zip"
		              label="ZIP Code *"
		              type = "number"
		              min = "0"
		              placeholder={this.state.zip}
		              onChange={this.onTypeEnter}
		              style={{background:"rgb(243,245,248)"}}
		            />
		          </div>
		          <div>
		          	<p className="mt3 ml2 mb1 gray gender">Gender</p>
		          	<p onClick={this.onOptionClick} id="male" className="f4 pointer ml2 br2 ph4 pv3 mb0 dib" style={{background: this.state.gender === "male" ? this.state.on_color : this.state.off_color, color: this.state.gender === "male" ? "white" : "gray"}}>Male</p>
		          	<p onClick={this.onOptionClick} id="female" className="f4 pointer ml2 br2 ph4 pv3 mb0 dib" style={{background: this.state.gender === "female" ? this.state.on_color : this.state.off_color, color: this.state.gender === "female" ? "white" : "gray"}}>Female</p>
		          	<p onClick={this.onOptionClick} id="other" className="f4 pointer ml2 br2 ph4 pv3 mb0 dib" style={{background: this.state.gender === "other" ? this.state.on_color : this.state.off_color, color: this.state.gender === "other" ? "white" : "gray"}}>Prefer not to Answer</p>
		          </div>
		          <p className="f4 mt4 b red tc">{this.state.error}</p>
		          <p onClick={onSubmit} className="br-100 purple ph3 pv3 shadow-2 pointer w-10 mt2" style={{"margin":"auto", "margin-top":"20px"}}><span class="arrow arrow-right"></span></p>
		        </div>
			</div>
		)
	}
}

export default Register1;